/*----------------------------------*\
    Header
\*----------------------------------*/
.header {
    z-index: 997;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;

    color: $color-white;
    font-family: $font-primary, $font-secondary;
    background-color: $color-light-gray;
    border-bottom: 2px solid $color-black;

    &__brand {
        font-size: 30px;
        margin-left: 25px;
        font-weight: 300;
        @include user-select(none);

        a {
            z-index: 500;
        }

        svg {
            z-index: -1;
            position: absolute;
            top: 10px;
            left: 23px;
            pointer-events: none;
            stroke-dasharray: 240;
            stroke-dashoffset: 240;
            @include transition(.3s ease-in-out);

            rect {
                @include transition();
                @include transform(scaleX(0))
            }
        }

        &:hover svg {
            @include transition(.25s);
            stroke-dashoffset: 0;

            rect {
                @include transform(scaleX(1))
            }
        }
    }

    &__links {
        margin-left: auto;
        float: right;
        z-index: 998;
        @include user-select(none);

        a {
            letter-spacing: 1px;
            color: inherit;
            font-size: 17px;
            font-weight: 700;
            text-transform: uppercase;
            margin: 5px 45px 5px 35px;
            padding: 0 10px 0 10px;
            background-color: transparent;
            border-radius: 2px;
            @include transition();

            &:hover {
                color: $color-black;
                background-color: $color-white;
                border-bottom-color: $color-white;
            }

            &:after {
                content: "";
                display: block;
                height: 2px;
                background-color: lighten($color-gray, 15%);
                width: 100%;
                @include transition();
            }

            &:hover:after {
                background-color: lighten($color-gray, 80%);
            }
        }

    }

    &__btn {
        z-index: 999;
    }

    @media #{$xs-down} {
        &__links {
            display: none;
        }

    }
}
