/*----------------------------------*\
    Backgruonds
\*----------------------------------*/
/* background properties */
.bg {

    /* background image */
    &--image {
        background: no-repeat 50% 50%;
        background-size: cover;
    }

    /* background-colors */
    @each $name, $value in $colors{
        &--#{$name}{
            background-color: #{$value};

            &.hover-d{
                @include transition();

                &:hover{
                    background-color: darken($value, $darken);
                }
            }
            &.hover-l{
                @include transition();

                &:hover{
                    background-color: lighten($value, $lighten);
                }
            }
        }
    }
}