.body {
    height: 100vh;

    background-color: $color-black;
    color: $color-white;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-family: $font-primary $font-secondary;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;

        h1, .heading {
            font-size: 6em;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 2px;
        }

        h2, h3, h4 {
           font-size: 2em;
        }

        p {
            font-size: 20px;
            letter-spacing: .05px;
            text-align: center;

            @media #{$sm-only} {
                font-size: 25px;
            }
        }

        br {
            content: " ";
            display: block;
            margin: 10px 0;
        }
    }

    &__button {
        background-color: $color-white;
        border-radius: 1px;
        letter-spacing: 0.5px;

        vertical-align: middle;
        text-align: center;

        font-weight: 700;
        text-transform: uppercase;

        padding: 10px 12px 10px 12px;
        cursor: pointer;

        border: 1px solid $color-gray;
        color: $color-white;

        @include transition();

        &.green {
            background-color: transparent;
            color: lighten($color-light-green, 10%);
            border: 1px solid $color-light-green;
        }

        &.green:hover {
            color: black;
            background-color: $color-green;
        }
    }
}